$(document).ready(function () {

	$('.to-action__button').on('click', function () {
		$('.calculator').show();
        $('.to-action').hide();
        
        if ($(window).width() < 769) {
            $('.bar').hide();
            $('.burger').hide();
        }


        $('.calculator__close').on('click', function(){
            $('.calculator').hide();
            $('.to-action').show();

            if ($(window).width() < 769) {
                $('.bar').show();
                $('.burger').show();
            }
        });

        $(window).scroll(function(){
            var scrolled = $(window).scrollTop();
            var header = $('.header').height();
    
            if(scrolled>header) {
                $('.calculator').slideUp();
                $('.to-action').slideDown();

                if ($(window).width() < 769) {
                    $('.bar').show();
                    $('.burger').show();
                }
            }
        });
	});

});
