$(function(){
    $(".sale__phone").mask("+7 (9 9 9) 9 9 9-9 9-9 9");


    $('.sale__name').on('keypress', function() {
      var that = this;
  
      setTimeout(function() {
          var res = /[^а-яА-Я]/g.exec(that.value);
          that.value = that.value.replace(res, '');
      }, 0);
  });
  });

  