(function () {

	$('.burger').on('click', function () {
		$('.menu').fadeToggle(150);
		$(this).toggleClass('burger__close');
	});

	$(window).scroll(function () {
		if ($(window).width() < 768) {
			var scrolled = $(window).scrollTop();

			if (scrolled > 100) {
				$('.menu').slideUp(150);
				$('.burger').removeClass('burger__close');
			}
		}

	});

})();
