(function () {


	function carousel(setting) {

		/* Scope privates methods and properties */
		let privates = {};
		let reset = false;

		let crs = this;


		/* Privates properties */
		privates.setting = setting;

		privates.sel = {
			"main": document.querySelector(privates.setting.main),
			"wrap": document.querySelector(privates.setting.wrap),
			"children": document.querySelector(privates.setting.wrap).children,
			"prev": document.querySelector(privates.setting.prev),
			"next": document.querySelector(privates.setting.next)
		};

		privates.opt = {
			"position": 0,
			"max_position": document.querySelector(privates.setting.wrap).children.length
		};

		// Control
		if (privates.sel.prev !== null) {
			privates.sel.prev.addEventListener('click', () => {
				this.prev_slide();
				reset = true;
			});
		}

		if (privates.sel.next !== null) {
			privates.sel.next.addEventListener('click', () => {
				this.next_slide();
				reset = true;
			});
		}

		this.prev_slide = () => {
			--privates.opt.position;

			if (privates.opt.position < 0) {
				privates.sel.wrap.classList.add('s-notransition');
				privates.opt.position = privates.opt.max_position - 1;
			}

			privates.sel.wrap.style["transform"] = `translateX(-${privates.opt.position}00%)`;
		};


		// Next slide
		this.next_slide = () => {
			++privates.opt.position;

			if (privates.opt.position >= privates.opt.max_position) {
				privates.opt.position = 0;
			}

			privates.sel.wrap.style["transform"] = `translateX(-${privates.opt.position}00%)`;
		};



		$(window).resize(function () {
			if (reset && $(window).width() > 1023) {
				privates.sel.wrap.style["transform"] = `translateX(0%)`;
				privates.opt.position = 0;
			}
		});


		//swipe

	}



	new carousel({
		"main": ".services",
		"wrap": ".services__wrap",
		"prev": ".services__svg_arrow_l",
		"next": ".services__svg_arrow_r"
	});


	$('.services__button').each(function (index, element) {
		$(this).on('click', function () {
			$('.calculator').show();
			$('.services').hide();

			$('.calculator__close').on('click', function () {
				$('.calculator').hide();
				$('.services').show();
			});
		});
	});


})();
